import { getBookById, getReviewsForBook, getAverageRating, getUserById } from "@/lib/data";
import { notFound } from "next/navigation";
import Image from "next/image";
import { Card, CardContent } from "@/components/ui/card";
import Rating from "@/components/ui/Rating";
import ReviewCard from "@/components/reviews/ReviewCard";
import ReviewForm from "@/components/reviews/ReviewForm";
import { Badge } from "@/components/ui/badge";
import RatingDistributionChart from "@/components/charts/RatingDistributionChart";
import { Separator } from "@/components/ui/separator";
import Recommendations from "@/components/ai/Recommendations";
import { Suspense } from "react";
import { Skeleton } from "@/components/ui/skeleton";
import type { Metadata } from 'next'
 
type Props = {
  params: { id: string }
}
 
export async function generateMetadata({ params }: Props): Promise<Metadata> {
  const id = params.id
  const book = await getBookById(id)
 
  return {
    title: book ? `${book.title} by ${book.author}` : 'Book Not Found',
  }
}

export default async function BookDetailsPage({ params }: { params: { id: string } }) {
  const book = await getBookById(params.id);

  if (!book) {
    notFound();
  }

  const reviews = await getReviewsForBook(book.id);
  const averageRating = getAverageRating(book.id);
  const addedByUser = await getUserById(book.addedBy);

  return (
    <div className="container mx-auto py-6 sm:py-8 px-4 sm:px-6 lg:px-8">
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 md:gap-12">
        <div className="md:col-span-1">
          <Card className="overflow-hidden md:sticky top-20">
            <Image
              src={book.coverImage}
              alt={`Cover of ${book.title}`}
              width={400}
              height={600}
              className="w-full object-cover"
              data-ai-hint="book cover"
              priority
            />
          </Card>
        </div>

        <div className="md:col-span-2">
          <Badge variant="secondary" className="mb-2">{book.genre}</Badge>
          <h1 className="text-3xl md:text-4xl font-headline font-bold tracking-tight">{book.title}</h1>
          <p className="text-lg md:text-xl mt-1 text-muted-foreground font-medium">{book.author}</p>
          <div className="flex items-center gap-4 mt-4">
            <Rating rating={averageRating} starClassName="h-6 w-6" />
            <span className="text-muted-foreground text-sm">{reviews.length} review(s)</span>
          </div>
          
          <Separator className="my-6" />

          <div className="prose dark:prose-invert max-w-none text-base">
            <p>{book.description}</p>
          </div>

          <div className="mt-4 text-sm text-muted-foreground space-y-1">
              <p>Published in {book.year}</p>
              {addedByUser && <p>Added by {addedByUser.name}</p>}
          </div>

          <Separator className="my-8" />

          <div>
            <h2 className="text-2xl font-headline font-semibold mb-6">Reviews</h2>
            <div className="grid grid-cols-1 xl:grid-cols-2 gap-8 mb-8">
                <div className="space-y-6">
                    {reviews.length > 0 ? (
                        reviews.map((review) => (
                        <ReviewCard key={review.id} review={review} />
                        ))
                    ) : (
                        <p className="text-muted-foreground">No reviews yet. Be the first to write one!</p>
                    )}
                </div>
                <div className="row-start-1 xl:col-start-2">
                    <Card>
                        <CardContent className="p-6">
                        <h3 className="text-lg font-semibold mb-2">Rating Distribution</h3>
                        <RatingDistributionChart reviews={reviews} />
                        </CardContent>
                    </Card>
                </div>
            </div>
            <ReviewForm bookId={book.id} />
          </div>

          <Separator className="my-12" />
          
          <div>
            <h2 className="text-2xl font-headline font-semibold mb-4">You might also like</h2>
             <Suspense fallback={<Skeleton className="h-48 w-full" />}>
                <Recommendations book={book} />
            </Suspense>
          </div>
        </div>
      </div>
    </div>
  );
}
